import React, { useState, useEffect } from 'react';
import { FiMenu, FiX, FiDownload } from 'react-icons/fi';
import { FloatingNav } from './ui/floating-navbar';

const Header: React.FC = () => {
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const [isScrolled, setIsScrolled] = useState(false);

    const toggleMenu = () => setIsMenuOpen(!isMenuOpen);

    useEffect(() => {
        const handleScroll = () => {
            setIsScrolled(window.scrollY > 50);
        };
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);

    const navItems = [
        { href: '/', label: 'Home' },
        { href: '/about', label: 'About' },
        { href: '/projects', label: 'Projects' },
        { href: '/contact', label: 'Contact' },
    ];

    return (
        <header className={`fixed w-full z-50 transition-all duration-300 ${isScrolled ? 'bg-darkModeBg shadow-md' : 'bg-transparent'}`}>
            <div className="container mx-auto px-4 sm:px-6 lg:px-8">
                <div className="flex items-center justify-between h-16">
                    <a href="/" className="text-xl font-bold text-purple-100">MEFTAH.</a>

                    <nav className="hidden md:flex space-x-6">
                        {navItems.map((item) => (
                            <a
                                key={item.href}
                                href={item.href}
                                className="text-lavender-100 opacity-50 hover:opacity-100 hover:text-purple-100 relative group transition-colors duration-300"
                            >
                                {item.label}
                                <span className="absolute inset-x-0 bottom-0 h-0.5 bg-purple-100 transform scale-x-0 transition-transform duration-300 ease-out group-hover:scale-x-100 origin-center"></span>
                            </a>
                        ))}
                    </nav>

                    <div className="hidden md:flex items-center">
                        <a href="#CV" className="flex items-center px-4 py-2 bg-pink-300 text-darkModeTxt rounded-md hover:bg-pink-400 transition-colors duration-200">
                            <FiDownload className="mr-2" /> CV
                        </a>
                    </div>

                    <button onClick={toggleMenu} className="md:hidden p-2 rounded-md text-lavender-100 hover:bg-lavender-400 transition-colors duration-200">
                        {isMenuOpen ? <FiX className="w-6 h-6" /> : <FiMenu className="w-6 h-6" />}
                    </button>
                </div>
            </div>

            {/* Mobile menu */}
            <nav className={`md:hidden fixed top-16 right-0 bottom-0 w-64 bg-darkModeBg shadow-lg transform transition-transform duration-300 ease-in-out ${isMenuOpen ? 'translate-x-0' : 'translate-x-full'}`}>
                <div className="flex flex-col h-full py-6 px-4 space-y-4">
                    {navItems.map((item) => (
                        <a
                            key={item.href}
                            href={item.href}
                            className="text-lavender-100 hover:text-purple-100 transition-colors duration-200"
                            onClick={toggleMenu}
                        >
                            {item.label}
                        </a>
                    ))}
                    <div className="mt-auto">
                        <a href="#CV" className="flex items-center w-full px-4 py-2 bg-pink-300 text-darkModeTxt rounded-md hover:bg-pink-400 transition-colors duration-200">
                            <FiDownload className="mr-2" /> Download CV
                        </a>
                    </div>
                </div>
            </nav>
        </header>
    );
};

export default Header;